import axiosInstance from '../../../utils/axios.middleware.js';

class Question {

	// Patient's Questions
	static getPatientDemoQuestions = () => {

		let apiPath = '/api/question/patient/demo';
		return axiosInstance.get(apiPath);

	};

	static addPatientDemoQuestions = (data) => {

		let apiPath = '/api/question/patient/demo';
		return axiosInstance.post(apiPath, {
			dynamicQuestions: data.dynamicQuestions,
			preDefinedQuestions: data.preDefinedQuestions
		});

	};


	// Caregiver's Questions
	static getCaregiverDemoQuestions = () => {

		let apiPath = '/api/question/caregiver/demo';
		return axiosInstance.get(apiPath);

	};

	static addCaregiverDemoQuestions = (data) => {

		let apiPath = '/api/question/caregiver/demo';
		return axiosInstance.post(apiPath, {
			dynamicQuestions: data.dynamicQuestions,
			preDefinedQuestions: data.preDefinedQuestions,
			patient_id: data.patient_id
		});

	};

	// Child's Questions
	static getChildDemoQuestions = () => {

		let apiPath = '/api/question/child/demo';
		return axiosInstance.get(apiPath);

	};

	static addChildDemoQuestions = (data) => {

		let apiPath = '/api/question/child/demo';
		return axiosInstance.post(apiPath, {
			dynamicQuestions: data.dynamicQuestions,
			preDefinedQuestions: data.preDefinedQuestions,
			patient_id: data.patient_id
		});

	};

	static getPatientScaleQuestions = () => {
		let apiPath = '/api/question/patient/scale';
		return axiosInstance.get(apiPath);
	};

	static addPatientScaleQuestions = (data) => {
		let apiPath = '/api/question/patient/scale';
		return axiosInstance.post(apiPath, {
			patient_id: data.patient_id,
			data: data.data
		});
	};

	static getCaregiverScaleQuestions = () => {
		let apiPath = '/api/question/caregiver/scale';
		return axiosInstance.get(apiPath);
	};

	static addCaregiverScaleQuestions = (data) => {
		let apiPath = '/api/question/caregiver/scale';
		return axiosInstance.post(apiPath, {
			patient_id: data.patient_id,
			data: data.data
		});
	};

	static getChildScaleQuestions = () => {
		let apiPath = '/api/question/child/scale';
		return axiosInstance.get(apiPath);
	};

	static addChildScaleQuestions = (data) => {
		let apiPath = '/api/question/child/scale';
		return axiosInstance.post(apiPath, {
			patient_id: data.patient_id,
			data: data.data
		});
	};

}

export default Question;