import Question from "../services/question/question.js";
import actionErrorHandler from '../../helpers/actionErrorHandler.js';

// Patient's Questions
export const getPatientDemoQuestions = () => {


	return Question.getPatientDemoQuestions()
		.then((response) => {
			return {
				status: response.status,
				data: response.data,
				message: response?.data?.message || "Success",
			};
		})
		.catch((e) => {
			return actionErrorHandler(e);
		});

};

export const addPatientDemoQuestions = (data) => {

	return Question.addPatientDemoQuestions(data)
		.then((response) => {
			return {
				status: response.status,
				data: response.data,
				message: response?.data?.message || "Success",
			};
		})
		.catch((e) => {
			return actionErrorHandler(e);
		});

};


